<template>
    <div id="accessory-addons">
        <div class="accessories-title">Accessory Addons</div>
        <div class="accessories-sub-title" v-if="datasets.length<1">
            <div class="d-flex ma-auto" style="width:fit-content">
                <v-progress-circular indeterminate/>
                <div class="mt-1 ml-2">Loading accessories...</div>
            </div>
        </div>
        <div class="accessories-sub-title" v-else-if="copier.datasets.length<1">(No accessories available for this product)</div>
        <div class="accessories-container pa-lg-5 pa-1" v-else>
            <v-row>
                <v-col :class="`pa-0 ${DatasetsUncategorized().length===1?'mx-auto':''}`" v-for="dataset in DatasetsUncategorized()" :key="dataset.id" cols="12" :lg="columnsLG" :md="columnsMD" :sm="columnsSM">
                    <v-card
                        elevation="2"
                        v-ripple rounded="lg"
                        class="ma-1 overflow-hidden"
                        style="word-break:break-all"
                        :id="`dataset-id-${dataset.id}`"
                        :color="dataset.selected?'rgba(125,188,103,0.45)':''">

                        <v-card-text class="pa-2" style="align-items:center;min-height:50px;overflow-y:hidden">
                            <AccessoryName
                                :dataset="dataset"
                                :accessory="Accessory(dataset)">
                                <template #actions>
                                    <AccessoryButtons
                                        :dataset="dataset"
                                        :datasets="copier.datasets"
                                        :accessory="Accessory(dataset)"
                                        @select="$emit('SelectDataset',dataset)"
                                        @unselect="$emit('UnselectDataset',dataset)">
                                    </AccessoryButtons>
                                </template>
                            </AccessoryName>
                        </v-card-text>
                    </v-card>
                </v-col>
            </v-row>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import CheckoutBtn from "@/components/catalog/CheckoutBtn.vue";
import AccessoryName from "@/components/configurator/AccessoryName.vue";
import AccessoryButtons from "@/components/configurator/AccessoryButtons.vue";
import NoCopierNoAccessories from "@/components/configurator/NoCopierNoAccessories.vue";
import Panel from "@/components/typography/Panel.vue";

export default {
    components: {
        Panel,
        CheckoutBtn,
        AccessoryName,
        AccessoryButtons,
        NoCopierNoAccessories,
    },
    props: ['copier'],
    data: () => ({
        tab: 'tab0',
    }),
    computed: {
        ...mapState([
            'accessories',
            'categories',
            'config_drawer',
            'datasets'
        ]),
        columnsLG() {
            return this.config_drawer ? 3:3
        },
        columnsMD() {
            return this.config_drawer ? 4:6
        },
        columnsSM() {
            return this.config_drawer ? 6:12
        }
    },
    methods: {
        DatasetsByCategory(category_id) {
            const datasets = [];
            this.copier.datasets.forEach((dataset) => {
                const accessory = this.accessories.find((accessory) => accessory.id === dataset.accessory_id);
                if (accessory.accessories_cat_id == category_id) {
                    if (dataset.product_id == this.copier.instance.id) {
                        datasets.push(dataset);
                    }
                }
            });
            return datasets;
        },
        DatasetsUncategorized() {
            const datasets = [];
            this.copier.datasets.forEach((dataset) => {
                const accessory = this.accessories.find((accessory) => accessory.id === dataset.accessory_id);
                if (dataset.product_id == this.copier.instance.id) {
                    datasets.push(dataset);
                }
            });
            return datasets.slice().sort((a, b) => {
                const accessoryA = this.Accessory(a);
                const accessoryB = this.Accessory(b);
                return accessoryA.name.localeCompare(accessoryB.name);
            });
        },
        Accessory(dataset) {
            return this.accessories.find((accessory) => accessory.id === dataset.accessory_id);
        },
    },
};
</script>

<style scoped lang="scss">
.accessories-title {
    font-size: 20px;
    text-align: center;
    @media(max-width: 1280px) {
        & {
            margin-bottom: 20px !important
        }
    }
}
.accessories-sub-title {
    font-size: 16px;
    text-align: center;
    margin: 10px 0;
    font-style: italic;
}
.accessories-container {
    padding: 20px;
}
</style>
