<template>
    <div class="d-flex align-center">
        <div :style="WidthAndHeight" :class="{'elevation-2':dataset?.selected}" class="d-flex align-center mr-3 accessory-image-wrapper">
            <v-img class="accessory-image" :width="is_mobile?35:60" :src="AccessoryImage(accessory)" alt="Accessory Image"/>
        </div>
        <div class="w-100">
            <span class="clip-text-2-lines accessory-name">{{accessory.name}}</span>
            <slot name="actions"/>
        </div>
    </div>
</template>

<script>
import {AccessoryMixins} from "@/mixins/AccessoryMixins";
import Tooltip from "@/components/typography/Tooltip.vue";
import {mapState} from "vuex";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    components: {Tooltip},
    props: ['accessory','dataset'],
    emits: ['onSelect', 'onRemove'],
    mixins: [AccessoryMixins,CommonMixins],
    computed: {
        ...mapState(['is_mobile']),
        WidthAndHeight() {
            if (this.is_mobile) {
                return "width:35px;height:35px"
            } else {
                return "width:60px;height:60px"
            }
        }
    }
}
</script>
<style>
@media(min-width: 900px) {
    .accessory-name {
        min-height: 40px;
    }
}
.accessory-image-wrapper {
    height: 50px;
    background: #ffffff;
    border-radius: 15px;
}
.accessory-image-wrapper .accessory-image {
    border-radius: 15px;
}
</style>
