<template>
    <ConfiguratorLayout>
        <template #main>
            <div class="position-relative">
                <Stepper :steps="stepper_items" :active_step="1" @addToCart="AddToCart"/>
                <div class="configurator-controls">
                    <Tooltip text="Toggle Sidebar">
                        <v-btn class="d-block ma-auto mb-lg-2" variant="text" size="40" icon="mdi-menu" @click="ToggleDrawer"/>
                    </Tooltip>
                    <CopierCart class="d-block mt-lg-5 ma-auto"/>
                    <Tooltip text="Zoom In">
                        <v-btn @click="ZoomIn" size="40" class="d-block ma-auto mt-lg-2" variant="text" icon="mdi-magnify-plus-outline"/>
                    </Tooltip>
                    <Tooltip text="Zoom out">
                        <v-btn @click="ZoomOut" size="40" class="d-block ma-auto mt-lg-2" variant="text" icon="mdi-magnify-minus-outline"/>
                    </Tooltip>
                </div>
            </div>

            <ImageStacks :product="copier.instance" :attachments="DatasetsAttached"/>

            <!--The Available Printer Accessories-->
            <DatasetsList :copier="copier" v-if="copier.instance" @SelectDataset="SelectDataset" @UnselectDataset="UnselectDataset"/>
        </template>

        <!--The added accessories on the right sidebar-->
        <template #drawer>
            <AddedAccessoryItem :title="copier.instance.name" subtitle="Main Body"/>
            <AddedAccessoryItem :title="Accessory(item).name" :subtitle="Accessory(item).part_number" v-for="item in DatasetsAttached" @remove="UnselectDataset(item)" :action="true"/>
            <NoAccessoryAddedYet v-if="!DatasetsAttached.length"/>
            <RemoveAccessoriesBtn v-if="DatasetsAttached.length" :count="DatasetsAttached.length" @onRemove="RemoveAllAccessories"/>
        </template>

        <!--Buttons in the Drawer's Appbar-->
        <template #drawerAppbar>
            <Tooltip text="Undo last action">
                <v-btn :disabled="!undo_list.length" @click="UndoLastAccessory" size="small" class="ml-3" icon="mdi-undo"/>
            </Tooltip>
            <Tooltip text="Redo last action">
                <v-btn :disabled="!redo_list.length" @click="RedoLastAccessory" size="small" class="ml-3" icon="mdi-redo"/>
            </Tooltip>
            <Tooltip text="Remove all accessories">
                <v-btn :disabled="!DatasetsAttached.length" size="small" class="ml-3" icon="mdi-refresh" @click="RemoveAllAccessories"/>
            </Tooltip>

            <v-spacer v-if="is_mobile"/>
            <Tooltip text="Hide Sidebar" v-if="is_mobile">
                <v-btn size="small" class="ml-3" @click="ToggleDrawer">Go back</v-btn>
            </Tooltip>
        </template>

        <template #drawerActions>
            <AddToQuoteBtn @onAdd="AddToCart"/>
        </template>
    </ConfiguratorLayout>

    <!--Conflict Modal-->
    <Modal color="transparent" v-if="conflicts.modal" :visible="true" :title="'Youre adding: '+conflicts.title" max_width="700" @close="CancelConflictAction">
        <v-container>
            <v-divider>{{conflicts.subtext}}</v-divider>
            <v-list class="mt-3">
                <v-list-item v-for="ds_item in conflicts.items">
                    <template v-slot:prepend>
                        <AccessoryName :accessory="Accessory(ds_item)"/>
                    </template>
                </v-list-item>
            </v-list>
        </v-container>
        <v-footer color="#f1f1f1" class="border-t pb-3 pt-3">
            <v-spacer/>
            <v-btn color="primary" @click="CallConflictAction">Okay</v-btn>
            <v-btn color="error ml-3" @click="CancelConflictAction">Cancel</v-btn>
            <v-spacer/>
        </v-footer>
    </Modal>
</template>

<script>
// Other Components
import {toRaw} from "vue";
import {mapActions, mapState} from "vuex";
import Modal from "@/components/misc/Modal.vue";
import StepTitle from "@/components/typography/StepTitle.vue";
import CheckoutBtn from "@/components/catalog/CheckoutBtn.vue";
import DatasetsList from "@/components/catalog/DatasetsList.vue";
import StepCardIcon from "@/components/typography/StepCardIcon.vue";
import StepCardTitle from "@/components/typography/StepCardTitle.vue";

// Mixins
import {CommonMixins} from "@/mixins/CommonMixins";
import {ProductMixins} from "@/mixins/ProductMixins";
import {AccessoryMixins} from "@/mixins/AccessoryMixins";

// Configurator Components
import ConfiguratorLayout from "@/layouts/ConfiguratorLayout.vue";
import CopierCart from "@/components/configurator/CopierCart.vue";
import ImageStacks from "@/components/configurator/ImageStacks.vue";
import ProductPrice from "@/components/configurator/ProductPrice.vue";
import BrandSelector from "@/components/configurator/BrandSelector.vue";
import AccessoryName from "@/components/configurator/AccessoryName.vue";
import AccessoryButtons from "@/components/configurator/AccessoryButtons.vue";
import MonthlyPrintsField from "@/components/configurator/MonthlyPrintsField.vue";
import AddedAccessoryItem from "@/components/configurator/AddedAccessoryItem.vue";
import CopierTypeSelector from "@/components/configurator/CopierTypeSelector.vue";
import NoAccessoryAddedYet from "@/components/configurator/NoAccessoryAddedYet.vue";
import RemoveAccessoriesBtn from "@/components/configurator/RemoveAccessoriesBtn.vue";
import NoCopierNoAccessories from "@/components/configurator/NoCopierNoAccessories.vue";
import AddToQuoteBtn from "@/components/configurator/AddToQuoteBtn.vue";
import Tooltip from "@/components/typography/Tooltip.vue";
import Stepper from "@/components/configurator/Stepper.vue";

export default {
    data: () => ({
        tab: 0,
        drawer: true,
        copiers_avail: [],

        // Holds the conflicted datasets
        conflicts: {
            modal: false,
            items: [],
            title: '',
            subtext:'',
            todos: []
        },

        // This is the copier selected by the user
        copier: {
            instance: null,
            datasets: []
        },

        // Undo and Redo (contains dataset IDs)
        undo_list: [],
        redo_list: [],

        // Steps
        stepper_items: [],
    }),
    components: {
        Stepper,
        Tooltip,
        AddToQuoteBtn,
        RemoveAccessoriesBtn,
        NoAccessoryAddedYet,
        Modal,
        StepTitle,
        CopierCart,
        ImageStacks,
        CheckoutBtn,
        DatasetsList,
        ProductPrice,
        StepCardIcon,
        BrandSelector,
        AccessoryName,
        StepCardTitle,
        AccessoryButtons,
        ConfiguratorLayout,
        MonthlyPrintsField,
        CopierTypeSelector,
        AddedAccessoryItem,
        NoCopierNoAccessories,
    },
    props: ['form'],
    mixins: [
        CommonMixins,
        ProductMixins,
        AccessoryMixins
    ],
    methods: {
        ...mapActions([
            'SetError',
            'SetSuccess',
            'SetAnyState',
            'SetCartItems',
            'SetSelectedCopier'
        ]),
        DatasetsByCategory(category_id) {
            const datasets = [];
            this.copier.datasets.forEach(dataset => {
                const accessory = this.accessories.find(accessory => accessory.id === dataset.accessory_id);
                if (accessory.accessories_cat_id == category_id) {
                    if (dataset.product_id == this.copier.instance.id) {
                        datasets.push(dataset);
                    }
                }
            });
            return datasets;
        },
        Accessory(dataset) {
            return this.accessories.find(accessory => accessory.id === dataset.accessory_id);
        },
        Dataset(id) {
            return this.datasets.find(dataset => dataset.id == id);
        },
        SetPrinter(copier) {
            this.copier.instance = copier;
            this.copier.datasets = this.datasets.filter(ds => ds.product_id == copier.id);
        },
        SelectDataset(dataset) {
            // There's a glitch when having multiple rule types (incompatible_with & require_all)
            // Even if there are conflicts/requirments, the dataset is still set to true.
            // Will leave it here in the meantime and will fix in the future.

            this.CancelConflictAction();
            const rules = JSON.parse(dataset.rules);
            if (rules.length<1) {
                dataset.selected = true;
                console.log("Went here 1");
                return this.SetSuccess('Accessory added');
            }
            rules.forEach(rule => {
                if (rule.type == 'incompatible_with') {
                    let conflicts = [];
                    rule.items.forEach(ds_id => {
                        if (this.Dataset(ds_id).selected) {
                            conflicts.push(this.Dataset(ds_id));
                        }
                    });
                    if (conflicts.length > 0) {
                        this.conflicts.items = conflicts;
                        this.conflicts.title = this.Accessory(dataset).name;
                        this.conflicts.subtext = 'The following accessories will be removed:';
                        this.conflicts.modal = true;
                        conflicts.forEach(conflict => {
                            this.conflicts.todos.push({
                                action: 'remove',
                                dataset: conflict
                            });
                        });
                        this.conflicts.todos.push({action:'add',dataset:dataset});
                    }
                    else {
                        dataset.selected = true;
                        if (!this.undo_list.includes(dataset.id)) this.undo_list.push(dataset.id);
                        this.SetSuccess('Accessory added');
                    }
                }
                else if (rule.type == 'require_one') {
                    let passed = false;
                    let items_txt = "<br><br>";
                    rule.items.forEach(ds => {
                        items_txt += '- '+this.Accessory(this.Dataset(ds)).name + '<br>';
                        if (this.Dataset(ds).selected) passed = true;
                    });
                    if (passed) {
                        dataset.selected = true;
                        if (!this.undo_list.includes(dataset.id)) this.undo_list.push(dataset.id);
                    }
                    else {
                        // Highlight the required items
                        dataset.selected = false;
                        rule.items.forEach(ds_id => {
                            let dataset_elem = document.querySelector(`#dataset-id-${ds_id}`);
                            if (dataset_elem) {
                                dataset_elem.classList.add('blink-yellow');
                                setTimeout(()=>{dataset_elem.classList.remove('blink-yellow')},2400);
                            }
                        });
                        this.CancelConflictAction();
                        return this.SetError(`This accessory requires one of the following. \n${items_txt}`);
                    }
                }
                else if (rule.type == 'require_all') {
                    let passed = true;
                    rule.items.forEach(ds => {
                        if (this.Dataset(ds).selected == false) {
                            passed = false;
                        }
                    });
                    if (passed) {
                        dataset.selected = true;
                        if (!this.undo_list.includes(dataset.id)) this.undo_list.push(dataset.id);
                    }
                    else {
                        this.conflicts.title = this.Accessory(dataset).name;
                        this.conflicts.subtext = "The following items will be added:";
                        this.conflicts.modal = true;

                        rule.items.forEach(ds => {
                            this.conflicts.items.push(this.Dataset(ds));
                            this.conflicts.todos.push({
                                action: 'add',
                                dataset: this.Dataset(ds)
                            });
                        });
                        this.conflicts.todos.push({
                            action: 'add',
                            dataset: dataset,
                        });
                    }
                }
                else if (rule.type == 'custom_css') {
                    dataset.selected = true;
                    if (!this.undo_list.includes(dataset.id)) this.undo_list.push(dataset.id);
                }
            });
        },
        AreThereConflicts(items) {
            return this.datasets.filter(item => items.includes(item.id) && item.selected);
        },
        CallConflictAction() {
            this.conflicts.todos.forEach(todo => {
                if (todo.action == 'remove') {
                    todo.dataset.selected = false;
                }
                if (todo.action == 'add') {
                    todo.dataset.selected = true;
                    this.SetSuccess('Accessory added');
                }
            });
            this.CancelConflictAction();
        },
        CancelConflictAction() {
            this.conflicts = {
                modal:   false,
                items:   [],
                title:   '',
                subtext: '',
                todos:   []
            };
        },
        DatasetsSelectedByCategory(cat_id) {
            const datasets = toRaw(this.datasets.filter(ds => ds.product_id == this.copier.instance.id));
            const list = [];

            datasets.forEach(dataset => {
                if (this.Accessory(dataset).accessories_cat_id == cat_id) {
                    if (dataset.selected === true) {
                        list.push(dataset);
                        console.log("selected: ", dataset.selected)
                    }
                }
            });
            return list;
        },
        UnselectDataset(dataset) {
            // Check 2 things:
            // 1. If the category requires at least 1 accessory
            // 2. If removing this accessory will cause a conflict
            // 3. If this accessory is being required by another accessory

            const conflicts = this.DatasetsThatRequiresThis(dataset);
            if (conflicts.length < 1) {
                dataset.selected = false;
                if (this.undo_list.includes(dataset.id)) {
                    const index = this.undo_list.findIndex(id => id == dataset.id);
                    this.undo_list.splice(index, 1);
                }
                if (!this.redo_list.includes(dataset.id)) {
                    this.redo_list.push(dataset.id);
                }
            }
            else {
                this.conflicts.items = conflicts;
                this.conflicts.title = this.Accessory(dataset).name;
                this.conflicts.subtext = 'The following accessories will be removed:';
                this.conflicts.modal = true;
                this.conflicts.todos.push({action:'remove',dataset:dataset});
                conflicts.forEach(conflict => {
                    this.conflicts.todos.push({
                        action: 'remove',
                        dataset: conflict
                    });
                });
            }
        },
        DatasetsThatRequiresThis(dataset) {
            const list = [];
            this.copier.datasets.forEach(ds => {
                const rules = JSON.parse(ds.rules);
                rules.forEach(rule => {
                    if (rule.type == 'require_one' || rule.type == 'require_all') {
                        rule.items.forEach(item => {
                            if (item == dataset.id && ds.selected) {
                                list.push(ds);
                            }
                        });
                    }
                });
            });
            return list;
        },
        RemoveAllAccessories() {
            this.copier.datasets.forEach(dataset => {
                dataset.selected = false;
            });
            this.redo_list = [];
            this.undo_list = [];
        },
        AddToCart() {
            const current_items = [...this.cart_items];
            current_items.push({
                product: JSON.parse(JSON.stringify(this.copier.instance)),
                datasets: JSON.parse(JSON.stringify(this.copier.datasets)),
                quantity: 1,
            });
            this.copier = {instance:null,datasets:[]};
            this.SetCartItems(current_items);
            this.SetSuccess("1 item(s) added to quote");

            // Select appropriate form to show
            this.FormSelector();

            // Show the form Modal
            this.SetState('form_modal', true);
            this.SetState('selected_copier', null);
        },
        ToggleDrawer() {
            this.SetAnyState({
                target: 'config_drawer',
                value: !this.config_drawer
            });
        },
        ZoomIn() {
            if (this.zoom_level < 2) {
                this.SetAnyState({
                    target: 'zoom_level',
                    value: this.zoom_level + 0.1
                });
            }
        },
        ZoomOut() {
            if (this.zoom_level > 0.5) {
                this.SetAnyState({
                    target: 'zoom_level',
                    value: this.zoom_level - 0.1
                });
            }
        },
        UndoLastAccessory() {
            // Grab the last item in the UNDO list
            const dataset_id = this.undo_list[this.undo_list.length-1];

            // Add it to REDO list
            this.redo_list.push(dataset_id);

            // Revert 'selected' property
            const index = this.datasets.findIndex(dataset => dataset.id === dataset_id);
            this.datasets[index].selected = false;

            // Delete the item in UNDO list
            this.undo_list.splice(-1, 1);
        },
        RedoLastAccessory() {
            // Grab the last item in the REDO list
            const dataset_id = this.redo_list[this.redo_list.length-1];

            // Add it to UNDO list
            this.undo_list.push(dataset_id);

            // Reselect via 'selected' property
            const index = this.datasets.findIndex(dataset => dataset.id === dataset_id);
            this.datasets[index].selected = true;

            // Delete the item in UNDO list
            this.redo_list.splice(-1, 1);
        },
        SetupStepper() {
            this.stepper_items = [];
            this.stepper_items.push("<i class='mdi mdi-printer'></i> "+this.copier.instance.model_number);
            this.stepper_items.push("<i class='mdi mdi-cog'></i> Accessories");
            this.stepper_items.push("<i class='mdi mdi-plus-circle-outline'></i> Add Product");
        }
    },
    computed: {
        ...mapState([
            'products',
            'datasets',
            'categories',
            'is_mobile',
            'cart_items',
            'zoom_level',
            'lease_terms',
            'accessories',
            'manufacturers',
            'config_drawer',
            'selected_copier',
            'addtocart_trigger'
        ]),
        DatasetsAttached() {
            if (!this.copier.instance) return [];
            return this.datasets.filter(dataset => {
                return dataset.product_id == this.copier.instance.id && dataset.selected;
            });
        }
    },
    mounted() {
        this.copier.instance = this.selected_copier;
        this.copier.datasets = this.datasets.filter(ds => +ds.product_id == +this.selected_copier.id);

        this.SetupStepper();
    },
    unmounted() {
        this.SetSelectedCopier(null);
        this.copier =  {
            instance: null,
            datasets: []
        }
    },
    watch: {
        addtocart_trigger() {
            this.AddToCart();
        },
        datasets() {
            this.copier.datasets = this.datasets.filter(ds => +ds.product_id == +this.selected_copier.id);
        },
        accessories() {
            this.copier.datasets = this.datasets.filter(ds => +ds.product_id == +this.selected_copier.id);
        }
    }
}
</script>
<style>
.v-application__wrap {
    min-height: fit-content;
}
.blink-yellow {
    animation: blinkYellow 0.8s linear 3;
}
@keyframes blinkYellow {
    0%, 100% {
        background-color: transparent;
    }
    50% {
        background-color: yellow;
    }
}
.configurator-controls {
    width: fit-content;
    text-align: center;
    position: absolute;
    padding: 5px;
    top: 0;
    right: 0;
    transition: .3s;
    z-index: 2;
}
</style>
