<template>
    <!--Disabled until Elie provides the pricing-->
    <!--<p class="font-weight-bold">${{accessory.price}}</p>-->
    <div class="d-flex align-center justify-space-between mt-md-1">
        <small class="d-block font-italic text-grey mt-1">{{accessory.part_number}}</small>
        <div>
            <Tooltip location="top" text="Remove this accessory" v-if="dataset.selected">
                <v-btn :disabled="default_attached" id="small-button" color="error" class="ml-5" width="55" size="x-small" variant="outlined" @click="$emit('unselect')">Remove</v-btn>
            </Tooltip>
            <Tooltip location="top" text="Add this accessory" v-else>
                <v-btn :color="ConfigElementsColor" class="ml-5" prepend-icon="mdi-plus" size="x-small" @click="$emit('select')">Add</v-btn>
            </Tooltip>
            <v-tooltip v-if="default_attached" target="small-button" activator="parent" location="right">
                This accessory is attached by default
            </v-tooltip>
        </div>
    </div>
</template>

<script>
import Tooltip from "@/components/typography/Tooltip.vue";
import {AccessoryMixins} from "@/mixins/AccessoryMixins";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    components: {Tooltip},
    emits: ['unselect','select'],
    mixins: [AccessoryMixins,CommonMixins],
    props: ['accessory','dataset','datasets'],
    computed: {
        default_attached() {
            return this.dataset.default_attached==1;
        },
    }
}
</script>
