<template>
    <Tooltip :text="`(${cart_items.length}) items in your cart`">
        <v-badge class="copier-cart" :content="cart_items.length" :color="ConfigElementsColor">
            <template #default>
                <v-btn size="40" variant="text" icon="mdi-cart-outline" @click="SetAnyState({target:'cart_modal',value:true})"/>
            </template>
        </v-badge>
    </Tooltip>
</template>
<script>
import Modal from "@/components/misc/Modal.vue";
import {mapActions, mapState} from "vuex";
import Tooltip from "@/components/typography/Tooltip.vue";
import {CommonMixins} from "@/mixins/CommonMixins";

export default {
    props: ['floated'],
    mixins: [CommonMixins],
    components: {Tooltip, Modal},
    computed: {
        ...mapState(['cart_items','cart_modal'])
    },
    methods: {
        ...mapActions(['SetFormStep','SetAnyState'])
    }
}
</script>
<style>
.copier-cart .v-badge__badge {
    top: -6px
}
</style>
